'use strict';

import { Component, EventEmitter, Output, Input } from '@angular/core';
@Component({
  selector: 'zippy',
  template: require('./zippy.html'),
  styles: [require('./zippy.css')]
})
export class Zippy {
  @Input() type = 'general';
  @Input() visible = false;
  @Input() empty = false;
  @Input() title;
  @Input() headless: boolean = false;
  @Output() open = new EventEmitter();
  @Output() close = new EventEmitter();
  toggle() {
    this.visible = !this.visible;
    if (this.empty) return;
    if (this.visible) {
      this.open.next({});
    } else {
      this.close.next({});
    }
  }
}



